<template>
    <div>

            <div class="chart-top">
                <div class="title h2">График продаж</div>

                <div class="chart-top__year">
                    <div class="year-counter">
                        <div v-if="showNav" class="p" @click="prevYear()"></div>
                        <div class="y">{{ currentYear }}</div>
                        <div v-if="showNav" class="p" @click="nextYear()"></div>
                    </div>

                </div>
            </div>

            <div class="chart" v-if="months.length > 0">
                <div class="chart__wrapper">
                    <div class="chart__col" v-for="month in months" :key="month.number">
                        <div class="chart__val" :style="{'height':month.percent + '%'}">
                            <div class="chart__tooltip">
                                <span class="cm">{{ month.date }}</span>

                                <span class="ct">Купонов погашено</span>
                                <span class="cb">{{ month.count }} шт.</span>

                                <span class="ct">Объём продаж</span>
                                <span class="cb">{{ month.price }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></span>

                                <span class="ct">Предоставлено скидок</span>
                                <span class="cb">{{ month.discount }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></span>

                                <span class="ct">Поступило в кассу</span>
                                <span class="cb">{{ month.income }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></span>
                            </div>
                        </div>

                        <div class="chart__month">
                            <span>{{ month.name }}</span>
                        </div>
                    </div>
                </div>
            </div>

            <div class="chart" v-else>
                <div class="chart__wrapper">
                    <div class="chart__col" v-for="month in monthsDefault">
                        <!-- у элементов с нулевым показателем нет блок chart__tooltip -->
                        <div class="chart__val" style="height: 0%; ">
                        </div>

                        <div class="chart__month">
                            <span>{{month}}</span>
                        </div>
                    </div>
                </div>
            </div>
        <!-- end chart -->
    </div>
</template>
    
    
<script>
    import {mapState} from 'vuex'

    export default {
        name: "Chart",
        components: {
        },
        props: {
            graph: {
                default: function () {
                    return []
                }
            },
            years: {
                default: function () {
                    return []
                }
            },
        },
        data() {
            return {
                defaultYear: 0,
                currentYear: 0,
                showNav: false,
                monthsDefault: [
                    'Январь',
                    'Февраль',
                    'Март',
                    'Апрель',
                    'Май',
                    'Июнь',
                    'Июль',
                    'Август',
                    'Сентябрь',
                    'Октябрь',
                    'Ноябрь',
                    'Декабрь',
                ]
            }
        },
        mounted: function () {
            let today = new Date();
            this.defaultYear = this.years[0] ? this.years[0] : 0;
            this.currentYear = this.defaultYear ? this.defaultYear : today.getFullYear();
            this.showNav = this.years.length > 1;

        },
        computed: {
            months: function () {
                let self = this;
                return this.graph.filter(month => month.year === self.currentYear);
            },
        },
        methods: {
            nextYear: function () {
                let currentIndex = this.years.indexOf(this.currentYear),
                    newIndex = currentIndex;

                newIndex++;

                if (newIndex < 0) {
                    newIndex = this.years.length - 1;
                } else if (newIndex > this.years.length - 1) {
                    newIndex = 0;
                }

                this.currentYear = this.years[newIndex];
            },
            prevYear: function () {
                let currentIndex = this.years.indexOf(this.currentYear),
                    newIndex = currentIndex;

                newIndex++;

                if (newIndex < 0) {
                    newIndex = this.years.length - 1;
                } else if (newIndex > this.years.length - 1) {
                    newIndex = 0;
                }

                this.currentYear = this.years[newIndex];
            }
        }
    }
</script>
